.todo-app {
  width: 500px;
  margin: auto;
  box-shadow: 0 0 20px 5px #0000001f;
  padding: 25px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
}
.todo-app input {
  background: #f4f7fe;
  border: 1px solid #f4f7fe;
  height: 50px;
  border-radius: 10px 0 0 10px;
}
.todo-app input:focus {
  box-shadow: none;
  outline: 0;
  border-color: #7b64ec;
}
.heading {
  margin-bottom: 40px;
}
ul li {
  display: flex;
  justify-content: center;
  justify-content: space-between;
  padding: 12px;
  background-color: #7b64ec14;
  border-radius: 8px;
  margin-bottom: 10px;
}
ul {
  overflow-y: auto;
  max-height: 300px;
}
.btn-danger {
  padding: 12px;
  border-radius: 10px;
  padding: 0 25px;
  height: 50px;
}
.input-wrapper button {
  background: #7b64ec;
  border-color: #7b64ec;
  border-radius: 0 10px 10px 0;
  height: 50px;
  padding: 0 25px;
  transition: 0.3s;
}
.input-wrapper button:hover {
  background: #6954d0;
  border-color: #6954d0;
}
.controls svg {
  font-size: 20px;
  margin-right: 15px;
  cursor: pointer;
}
.alert.danger {
  background: #dc3545;
  padding: 10px;
  color: #fff;
  border-radius: 10px;
}
.alert.success {
  background: #18b459;
  padding: 10px;
  color: #fff;
  border-radius: 10px;
}
.delete svg {
  color: #e91e63;
}
.edit svg {
  color: #483b8c;
}
